import React, { useState } from "react";
import { Link } from "react-router-dom";

import useMenuToggle from "./function/useMenuToggle";

const Navigation = () => {
  const [menuToogle, toogleMenu] = useMenuToggle();
  return (
    <React.Fragment>
      <div
        className={!menuToogle ? "menu-toggle" : "menu-toggle open"}
        onClick={toogleMenu}
      >
        <div className="icon"></div>
      </div>
      <div className={!menuToogle ? "main-menu" : "main-menu opened"}>
        <div className="contant-info">
          <div>
            <a href="mailto:hello@cre8.com">hello@cre8.com</a>
          </div>
          <div>+10 38093888</div>
        </div>
        <div className="menu-links">
          <ul>
            <li>
              <Link to="/" onClick={toogleMenu}>
                Home
              </Link>
            </li>
            <li>
              <Link to="/about" onClick={toogleMenu}>
                About
              </Link>
            </li>
            <li>
              <Link to="/work" onClick={toogleMenu}>
                Work
              </Link>
            </li>
            <li>
              <Link to="/blog" onClick={toogleMenu}>
                Blog
              </Link>
            </li>
            <li>
              <Link to="/contact" onClick={toogleMenu}>
                Contact
              </Link>
            </li>
          </ul>
        </div>
        <div className="social-media">
          <div className="social-link-holder">
            <a href="#">Dribbble</a>
          </div>
          <div className="social-link-holder">
            <a href="#">Instagram</a>
          </div>
          <div className="social-link-holder">
            <a href="#">Twitter</a>
          </div>
          <div className="social-link-holder">
            <a href="#">Facebook</a>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Navigation;
