import React from "react";
import Header from "./header/Header";

import DummyProject from "./dummy/DummyProject";
import ProjectBox from "./components/ProjectBox";
import ProjectForUs from "../shared/Quote/ProjectForUs";
import Clients from "../shared/Clients/Clients";

const Work = () => {
  let numberOfContainer = [];
  for (let i = 1; i <= Object.keys(DummyProject).length / 2; i++) {
    numberOfContainer.push(i);
  }
  console.error("CONTAINER", numberOfContainer, Object.keys(DummyProject));
  return (
    <>
      <Header />
      {numberOfContainer.map(number => {
        return (
          <div className="container-fluid box-content" key={number}>
            <div className="row">
              {Object.keys(DummyProject)
                .slice(number - 1, number + 1)
                .map(project => {
                  return (
                    <ProjectBox
                      title={DummyProject[project].title}
                      image={DummyProject[project].image}
                      url={project}
                    />
                  );
                })}
            </div>
          </div>
        );
      })}
      <Clients />
      <ProjectForUs />
    </>
  );
};

export default Work;
