import React from "react";

import history from "../../history";

import DummyPost from "../dummy/DummyPost";

const Post = props => {
  const images = require.context("../../assets/images/", true);
  let postImage;
  if (DummyPost[props.match.params.postId]) {
    postImage = images("./" + DummyPost[props.match.params.postId].image);
  }
  window.scrollTo(0, 0);
  if (DummyPost[props.match.params.postId]) {
    return (
      <div className="container-fluid post-section">
        <div className="row">
          <div className="col">
            <div className="post-header">
              <div className="text-holder">
                <div className="post-info-holder">
                  <div
                    className="link-holder"
                    onClick={history.goBack}
                    style={{ cursor: "pointer" }}
                  >
                    &#x3C; Back
                  </div>
                  <div className="post-info">
                    <div className="date">Aug 22, 2019</div>
                    <div className="auther">by Tuna Ozmen</div>
                  </div>
                </div>
                <div className="title">
                  {DummyPost[props.match.params.postId].title}
                </div>
                <div className="text">
                  {DummyPost[props.match.params.postId].description}
                </div>
              </div>
              <div className="img-holder">
                <img src={postImage} alt="" />
              </div>
            </div>
            <div className="post-content">
              <h1>Going beyond the screen</h1>
              <p>
                At vero eos et accusamus et iusto odio dignissimos ducimus qui
                blanditiis praesentium voluptatum deleniti atque corrupti quos
                dolores et quas molestias excepturi sint occaecati cupiditate
                non provident, similique sunt in culpa qui officia deserunt
                mollitia animi, id est laborum et dolorum fuga. Et harum quidem
                rerum facilis est et expedita distinctio.
              </p>

              <h1>Sketch vs Adobe XD</h1>
              <p>
                Nam libero tempore, cum soluta nobis est eligendi optio cumque
                nihil impedit quo minus id quod maxime placeat facere possimus,
                omnis voluptas assumenda est, omnis dolor repellendus.
                Temporibus autem quibusdam et aut officiis debitis aut rerum
                necessitatibus saepe eveniet ut et voluptates repudiandae sint
                et molestiae non recusandae. Itaque earum rerum hic tenetur a
                sapiente delectus, ut aut reiciendis voluptatibus maiores alias
                consequatur aut perferendis doloribus asperiores repellat.
              </p>

              <h1>Perfection is not a myth</h1>
              <p>
                At vero eos et accusamus et iusto odio dignissimos ducimus qui
                blanditiis praesentium voluptatum deleniti atque corrupti quos
                dolores et quas molestias excepturi sint occaecati cupiditate
                non provident, similique sunt in culpa qui officia deserunt
                mollitia animi, id est laborum et dolorum fuga. Et harum quidem
                rerum facilis est et expedita distinctio.
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return <h1>404</h1>;
  }
};

export default Post;
