import React from "react";

import team1Img from "../../assets/images/team1.png";
import team2Img from "../../assets/images/team2.png";
import team3Img from "../../assets/images/team3.png";
import team4Img from "../../assets/images/team4.png";
import team5Img from "../../assets/images/team5.png";
import team6Img from "../../assets/images/team6.png";
const Team = () => {
  return (
    <div className="container-fluid team-section">
      <div className="row">
        <div className="col">
          <div className="extra-lg-text">
            <span>behind every</span>
            <br />
            <span>great project,</span>
            <br />
            <span>cre8 team!</span>
          </div>
        </div>
      </div>
      <div className="team-photos">
        <div className="team-photos-holder">
          <div className="photo-holder">
            <img src={team1Img} alt="" />
          </div>
          <div className="photo-holder">
            <img src={team2Img} alt="" />
          </div>
          <div className="photo-holder">
            <img src={team3Img} alt="" />
          </div>
          <div className="photo-holder">
            <img src={team4Img} alt="" />
          </div>
          <div className="photo-holder">
            <img src={team5Img} alt="" />
          </div>
          <div className="photo-holder">
            <img src={team6Img} alt="" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Team;
