import React from "react";

import Header from "./components/Header";
import PostBox from "./components/PostBox";

import DummyPost from "./dummy/DummyPost";
import ProjectForUs from "../shared/Quote/ProjectForUs";

const Blog = () => {
  return (
    <>
      <Header />
      <div className="container-fluid blog-section">
        <div className="row">
          <div className="col">
            {Object.keys(DummyPost).map((post, index) => {
              return (
                <PostBox
                  key={index}
                  title={DummyPost[post].title}
                  description={DummyPost[post].description}
                  image={DummyPost[post].image}
                  url={post}
                />
              );
            })}
          </div>
        </div>
      </div>
      <ProjectForUs />
    </>
  );
};

export default Blog;
