import React from "react";
import Hubble from "./projects/Hubble";

const BackEndBox = () => {
  return (
    <div className="container-fluid box-content">
      <div className="row">
        <div className="col-md-6">
          <Hubble />
        </div>
        <div className="col-md-6">
          <div className="boxy primary-color">
            <div className="row">
              <div className="col">
                <h1 className="title">
                  backend <br /> development
                </h1>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                <div className="text">
                  <ul>
                    <li>Content Management System(CMS)</li>
                    <li>E-commerce</li>
                    <li>Customer Relationshop Management(CRM)</li>
                    <li>Node.js / Python / Java</li>
                    <li>SQL / NoSQL / GraphQL</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BackEndBox;
